        printedIn: "Bentley, <i>The Cult of the Superman</i> (Gloucester: Peter Smith)",
        issueOrVolume: null,
        pageRange: "v",
        year: 1944,
        monthAndDay: null,
        textProvided: "When a Scot or an Irishman wishes to criticize England, he settles in London and is paid by an English publisher to do the job ... the philosopher of progress often finds that progress has been suspended for his own lifetime ... Marx and Hegel are agreed upon the metaphysics of the escalator ... The nineteenth century gave us the word Nihilism. It is rebellion chemically pure. Such quotations give an idea of the freshness and pungency of Mr. Bentleys writing. But this work is something much more than a collection of wisecracks. It is the detailed and documented study of what the Author calls Heroic Vitalism  in fact, of the source from which most of the great anti-democratic movements of our own day proceed. Carlyle, Nietzsche, Shaw, Stefan George and D. H. Lawrence are among the specimens; on Wagner, Mr. Bentley seems to me especially helpful. The Author, though sternly critical, has a sympathy which I cannot emulate with those elements in Heroic Vitalism which really deserve a serious answer, and this enables him to make a constructive book out of what might easily have been a mere chamber of horrors.",
