        title: "Going into Europe: A Symposium",
        printedIn: "<i>Encounter</i>",
        issueOrVolume: "XIX",
        pageRange: "57",
        year: 1962,
        monthAndDay: "December",
        textProvided: "I suspect that the decision about the Common Market is very like that which confronts the losing chess-player: Which of your rooks will you lose? As for the larger issues, I want what we certainly shant get  a super-national state built out of units far smaller than the existing nations: units like Wessex and Picardy, not like Britain (a horrid word) and France. Units so small, and real, would then safely develop the greatest local diversity of language, culture, and custom. England did not achieve unity by a League of Barons. The Barons were (for all practical purposes) eliminated; the towns, shires, and even parishes, survived. That is our model.",
