        textProvided: "The prose version of Dymer.1916 (?).<br/>The Redemption of Ask.(In two parts, Lucrece metre.)Oct. and Nov. 1918<br/>The Red Maid (ballad).1920<br/>Dymer begun.April 2nd, 1922<br/>Canto I finished.May 11th, 1922<br/>Canto II finished.June 1st, 1922<br/>Canto II (A-version) abandoned.June 22nd, 1922<br/>Canto III (B) finished.June 29th, 1922<br/>Lyrical Epilogue for Dymer attempted.July 8th, 1922<br/>Canto IV finished.October 8th, 1922<br/>Canto V finished.March 25th, 1923<br/>Canto V recopied and corrected.June 27th, 1923<br/>Kirkian stanzas done for Canto VI.June 28th, 1923<br/>Kirkian episode abandoned.June 29th, 1923<br/>Cantos VI and VII (A-version) finished.September 8th, 1923. (Village shop and boat.)<br/>Cantos VI and VII condemned by Harwood.October 21st, 1923<br/>Canto VI (B) finished.October (?) 1923. (Old Welkin and boat.)<br/>Canto III (C) finished.January 22nd, 1924. (The cow version.)<br/>Canto VI (C) finished.March 25th, 1924<br/>Canto IX finished.March 25th, 1924<br/>Canto VII (B) begun.April 6th, 1924<br/>Fresh start on Canto VII (B).April 10th, 1924<br/>Canto VII (B) finished.(Complete text now in existence. Condemned.)April 28th, 1924<br/>Canto VI (D) started.April or May 1924<br/>Canto VI (D) finished.Before May 23rd, 1924<br/>Canto VII (C) and VIII.Long Vacation 1925 (?)<br/>Accepted by Dents.April 1st, 1926",
