        pageRange: "11-12",
        year: 1985,
        monthAndDay: null,
        textProvided: "White as we know is of good qualitys as a frog, but strange to say is of poor lineage. Many people are under an error concerning his family history. He can be traced to the Bigs. His derect parents were farmers in the country of Frog-land, but his grand-father was the brother of Bigs father: thus Marshel Wite is conected with the famous Sir Big but was much younger. Mr Little was borne at slimy-bay in the reign of King Mouse the Good. Wite at the age of 10 left his school, and served Tom Anderson for 13 years. (Tom Anderson was a millar.) No sooner had he left Tom Anderson than he went into the Army.",
