        pageRange: "62",
        year: 1992,
        monthAndDay: null,
        textProvided: "It would be almost safe to say that no book like this has ever been written. If Ariosto rivalled it in invention (in fact he does not) he would still lack its heroic seriousness. No imaginary world has been projected which is at once so multifarious and so true to its own inner laws; none so seemingly objective, so disinfected from the taint of an authors merely individual psychology; none so relevant to the actual human situation yet so free from allegory. And what fine shading there is in the variations of style to meet the almost endless diversity of scenes and characterscomic, homely, epic, monstrous, or diabolic!",
        type: "Prose",
        notes: null
