        textProvided: "<i>Echeneis</i> 1. Adj. -, ship-detaining. Hence used of lack of winds, or unfavourable winds (Aeschylus <i>Agamemnon</i> 149.) 2. As Noun: a shell-fish credited with the power of holding a ship back = Lat. <i>echeneis remora</i>. For which cf. Donne, <i>Elegy</i> XVIII, 58; <i>Letter to Sir H. Wootton,</i> 8; Griersons note on the Letter; also Thos. Browne, <i>Errors</i> III, cap. 28 last para. but two.",
