        pageRange: "18",
        year: 1998,
        monthAndDay: null,
        textProvided: "[Jack and Warnie had time, however, in those few weeks to discover]<br/><br/>the secret dark hole upstairs<br/><br/>[and to claim it for their own ...An unfinished story written later by Jack suggests what the hole looked like after a year or two. It was a narrow room where trunks and suitcases were stored, where spiders hibernated or spread their webs and dead flies littered the windowsills. Another little room contained a wobbly three-legged table, and still another room, a pile of torn canvas and stacks of old newspapers. But the real treasure of the place, according to Jacks story, was a knee-high]<br/><br/>miniature mountain<br/><br/>[of poems, plays, stories, and songs that the hero of Jacks story had written, and, with them, dusty pictures that he had drawn long before]<br/><br/>only because he did not know how to write. This pile of rubbish was my treasure, my religion, for it was my past, and the past was all I had yet made my own.<br/><br/>[But there was another treasure, a dark corner where, if one knelt down, particularly on a lonely day, he would hear, not only the gurgling, hissing, and splashing of the cisterns, but also the wind going through the]<br/><br/>inwards<br/><br/>[of the house, between walls and through ceiling and rafters; and he would feel an emptiness occupied by the terrible Lar, Roman god of houses. Kneeling there, one could easily believe that the soul of the house was in the imprisoned wind.]",
