        textProvided: "It is hoped that this little selection from my works, from which all objectionable matter has been carefully excluded, will be found specially suitable for Sunday and family reading, and also to the higher forms of secondary schools.",
        type: "Prose",
        notes: ["Also printed in <i>The Collected Poems of C.S. Lewis: A Critical Edition,</i> ed. Don W. King (Kent, OH: Kent State University Press, 2015), 460; George Herbert, <i>The Temple</i> in <i>C.S. Lewiss List: The Ten Books That Influenced Him Most,</i> eds. David Werther and Susan Werther (New York and London: Bloomsbury Academic, 2015), 74 note 25; and Don W. King, C.S. Lewis and George Herberts The Temple, <i>Sehnsucht</i> vol. 14 (2020), 148 note 23.", "According to Walter Hooper (<i>Poems</i> 1994), fourteen of [Lewiss] religious lyrics were sent to Owen Barfield during the summer of 1930 under the general title Half Hours with Hamilton, and they are some of the most beautiful poems Lewis wrote. Most of these same poems were to appear a couple of years later in his semi-autobiographical <i>The Pilgrims Regress</i> (1933). They were always Lewiss favourites of his own poems (xv)."]
