        title: "Suggested revisions of Psalms I-XLI (10 March 1959), LXVII-LXXXI, and LXXXIV-LXXIX for the Archbishops Commission to Revise the Psalter",
        printedIn: "George Musacchio, C.S. Lewis, T.S. Eliot, and the Anglican Psalter, <i>SEVEN: An Anglo-American Literary Review</i>",
        issueOrVolume: "Vol. 22",
        pageRange: "134",
        year: 2005,
        monthAndDay: null,
        textProvided: "[47]<br/><br/>On 10 March 1959, just a few weeks after this initial meeting, Lewis sent the COmmissions secretary eleven pages of his suggested revisions of Psalms I-XLI, written in his own hand. In his small cursive script, complete with cross-outs and circled footnotes, Lewis considers such things as accuracy, clarity, rhythm, alliteration, and figurative language. In Psalm 11:3 he notes an illogical image. Where Coverdale has For the foundations will be cast down, Lewis notes: We cant have what is already the lowest (foundations) cast <i>down;</i> its like talking about falling off the floor (2, n. 2). He suggests If the foundations be removed (2). Though concerned with accuracy, he is not interested in strict literalism. In Psalm 4:8 (subsequently published as 4:7) he prefers as joyful as to the accurate <i>more joyful than,</i> which will produce an inferior rhythm (1, n. 1). And of Psalm 19:3 he says, Leave it alone: it is only <i>verbally</i> false to the original (5, n. 1). <br/><br/>[52]<br/><br/>We glanced earlier at Lewiss suggestions for Psalms 1-41, submitted in March 1959; later he sent handwritten suggestions for Psalms 67-81 (2 pages) and 84-89 (2 pages). In all three lists his emendations, like Eliots, contain plenty of instances in which he says the equivalent of Keep Coverdales wording. <i>The Revised Psalter</i> (1964) contains at least six retentions of Coverdale recommended by Lewis; three others did not make the final version. One loss is in Psalm 25:4, where Coverdale has Lead me forth in thy truth, and learn me: for thou art the God of my salvation. Of course <i>teach</i> is the obvious modern replacement for <i>learn,</i> but Lewis objects. To change <i>learn</i> into <i>teach</i> wd. be pedantic pseudo scholarship, he writes. <i>Learn</i> (wh. was good enough for Mr. Badger) will mislead no-one (6, n. 3).",
