        year: 2015,
        monthAndDay: "10 December",
        textProvided: "https://www.christianitytoday.com/ct/2015/december-web-only/cs-lewis-secret-agent.html",
        type: "Prose",
        notes: ["Some of this essay has been quoted in Suzanne Bray, The Exact Programme a Particular Country Wishes to Have: C.S. Lewiss Literary Broadcast for Iceland, <i>Sehnsucht,</i> vol. 10 (2016), 21-32."]
