        monthAndDay: null,
        textProvided: false,
        type: "Prose",
        notes: ["In previous scholarship this 123-page document (Wade MS-76, incorrectly labeled The Moral Good - Its Place Among the Values) had been misidentified as being Lewiss written lectures for his series The Good, its position among the values (Michaelmas Term 1924) and Moral Good, its position among values (Hilary Term 1925). In fact they are his notes for philosophy tutorials on the epistemology of Leibniz, Locke, Berkeley, Hume, and Descartes.", "Excerpts quoted in Walter Hooper, To the Martlets, <i>C.S. Lewis: Speaker and Teacher,</i> ed. Carolyn Keefe (Grand Rapids: Zondervan, 1971), 52. Lewis summarizes Book I of Leibnizs Sur lEntendement Humain.", "Excerpts quoted in James Patrick, <i>The Magdalen Metaphysicals: Idealism and Orthodoxy at Oxford 1901-45</i> (Macon: Mercer University Press, 1987), 116-117. The excerpt is an explanation of Berkeleys idealism.", "Excerpts quoted in James Como, <i>Branches to Heaven: The Geniuses of C.S. Lewis</i> (Dallas, TX: Spence Publications, 1998), 76. Text as follows, from his critique of Berkeley: to ask what is in my mind while I am thinking? usually means to stop the real thinking and then introspect: and then I could naturally find only the irrelevant pictures or words which, as a matter of psychological fact, do accompany the thinking. ...Does all introspection always leave out the important things?", "Excerpts quoted in Adam Barkman, <i>C.S. Lewis and Philosophy as a Way of Life </i>(Cheshire, CT: Zossima Press, 2009), 115, 163-164, 219-224, 240, 254-255, 296-297, 330-332."]
