        textProvided: "The next thing I remember was coming back from Malvern. On the way I met a big cart driven by a girl who had no clothes on. She had very light brown hair: but dark skin, pink-brown like sand. I smiled at her in the confidential way one might smile at a girl when youd seen a hole in her stocking, and she smiled back in just the same way, as much as to say, Yes, I know. Isnt it a scream! Then I went back to Malvern and woke uphaving seen the girl again, this time in the distance beyond the river, with other people in the cart.",
