        textProvided: "[During the Christmas holidays of 1907 nine-year-old Jack began the first of his diaries. Or, more accurately, his first autobiography as it bears the title <i>My Life</i>. Jack intended that the Life should be read by all in the house and it is dedicated]<br/><br/>To Miss Maude Scott <br/><br/>[That was to prove very embarrassing because in the first paragraph he writes]<br/><br/>I have a lot of enymays, however there are only 2 in this house they are called Maude and Mat, Maude is far worse than Mat, but she thinks she is a saint  I HATE Maude. <br/><br/>[He goes on to say:]<br/><br/>Papy of course is the master of the house, and a man in whom you can see strong Lewis features, bad temper, very sensible, nice wen not in a temper. Mamy is like most middle-aged ladys, stout, brown hair, spectaciles, kniting her cheif industry etc. etc. I am like most boys of 9 and I am like Papy, bad temper, thick lips, thin and generally weraing a jersey. <br/><br/>[After describing the pets (a mouse, a canary and a dog) he mentions his paternal grandfather:]<br/><br/>I have left out an important member of the family namly my grand-father, who lives in a little room of his own up-stairs he is a nice old man in some ways but he pitys him-self rather much, however all old people do that.<br/><br/>[So great was his affection for Warnie that there is a section of the Life entitled]<br/><br/>Part IV.  How Warnie came home.<br/><br/>[As, however, Warnie had not yet arrived from Wynyard, Jack went on to write:]<br/><br/>I am still looking forward to Wanries homecoming which is always a great event in our house.  You see I had to wait for something to happen before I could write about it, and I put down Part IV How Warnie came home with out thinking therefore I shall have to fill up Part IV with other things. I have yet left out another important person who plays a large part in my life, namely Miss Harper who is my governess. She is fairly nice FOR a governess, but all of them are the same. Miss Harper has fair hair, blue eyes, and rather sharp features, she generally wears a green blouse and a dress of the same hue.<br/><br/>[Included in the Life was a drawing of Miss Harper with a balloon coming down from her lips which contained the words]<br/><br/>Dont say cant to me, Jacksie.<br/><br/>[Notebook II overlaps with number I and Jack scribbled in it,]<br/><br/>I often wish that I was leading a more use-ful life.",
