        printedIn: "Arend Smilde, Why C.S. Lewis did not become a philosopher: Biographical Introduction in C.S. Lewis: Tutor and Lecturer in Philosophy: Philosophical Notes (1924), eds. Norbert Feinendegen and Arend Smilde, <i>Inklings Studies Supplements</i>",
        issueOrVolume: "no. 2",
        pageRange: "73-74",
        year: 2021,
        monthAndDay: null,
        textProvided: "[On 19 June Lewis noted that one student, Ziman,]<br/><br/>is taking P.P.E. next term and will be coming to me for ordinary tuition: he wants however to take Kant as his special subject. He knew from what I had often said to him before that I was not a Kantian, but asked if I would be compos Kantis next term. I said that I should follow Carritts example in handing Kantians over to Ewing. Ziman however showed a strong disinclination to that solution and wanted to go to Paton: but this could not be arranged while Carritt was ill and so we had to let the matter stand.<br/><br/>[Moreover, that same morning Lewis had been invited to]<br/><br/>take Carlyles English pupils next term when Carlyle is away<br/><br/>[and he visited Carlyle immediately after meeting Ziman. Yet, for whatever reason, including perhaps the expected workload, Lewis confined himself to the philosophy job accepted from Carritt.]",
