        issueOrVolume: "vol. 10, no. 1",
        pageRange: "8",
        year: 2020,
        monthAndDay: "April",
        textProvided: "Sept 5th 1934<br/><br/>Dear Onions<br/><br/>Will this do? If so, and if you want it typewritten, you can either send it back to me for that purpose, or else charge it to me at the Broad St. typist. I find it a little hard to be quite sure what Chambers is up to  so there may be more than one <i>ignoratio elenchi</i> in what I have written.<br/><br/>Yours<br/><br/> C. S. Lewis<br/><br/>He <i>is</i> a Professor, isnt he?",
        type: "Letter",
