        pageRange: "8-9",
        year: 2020,
        monthAndDay: "April",
        textProvided: "Magdalen College, Oxford<br/><br/>Dear Onions<br/><br/>The passage in Aquinas bearing out your interpretation of Miltons <i>in mist,</i> is Sum.Theol. Pars I Quaest 52. Art 3. <i>Ad quintum</i><br/><br/>non fuit vera comestio sed figurativa spiritualis comestionis. [it was not a true eating, but figurative of spiritual eating.]<br/><br/>I hope the crudity of my expression of first doubts was not impudent. I have learned to come to you for information with such keen despatch of real Hunger and concoctive heate that sometimes I forget my table manners.<br/><br/>We didnt succeed in aiming Foxs mind and knowledge at <i>our</i> target did we? Yours<br/><br/>C. S. Lewis",
