        issueOrVolume: null,
        pageRange: "29",
        year: 1981,
        monthAndDay: null,
        textProvided: "No. I <i>dont</i> believe people sat down and made up mythology. There are both divine and diabolical elements in it. And anyway what <i>is</i> the process we describe as making up[?] I am willing to believe, with Henry More (the Cambridge Platonist) that some, at least, of the gods were good spirits as innocent of the worship idolatrously offered them as Peter and Mary are of that offered them by the Papists. (The simile is his.) But of course we know nothing. Its a view good enough to base a romance onnot part of mythology.",
        type: "Letter",
