        printedIn: "William Griffin, <i>Clive Staples Lewis: A Dramatic Life</i> (San Francisco, CA: Harper & Row)",
        issueOrVolume: null,
        pageRange: "284",
        year: 1986,
        monthAndDay: null,
        textProvided: "7/27/48<br/>Dear Vidler  <i>Peccavi.</i> Dont print the letter I sent you this morning. I dont yet think it was unjust but I am sure it was uncharitable. I was momentarily transported by the spectacle of apparent terrier revealing itself as rabbit as the first threat. I have now repented. But, Grrr! What a little sneak!<br/><br/>Yrs<br/>C. S. Lewis",
        type: "Prose",
        notes: ["Reprinted in the UK edition of this book: William Griffin, <i>C.S. Lewis: The Authentic Voice</i> (Tring: Lion Publishing, 1988), [page range unknown].", "The earlier letter referred to is in <i>Collected Letters</i> 2."]
