        year: 2009,
        monthAndDay: null,
        textProvided: "Each book by Empson read, becomes a land<br/>Transformed by some malign enchanters word;<br/>Each breaking land a <u>double-entendre</u> yields<br/>And teeming Mares build nest in all his fields.",
